.page-footer {
  width: 100%;
  border-top: 1px solid #e8e8e8;
  border-bottom: 1px solid #e8e8e8;
  margin-top: 40px;
}
.content {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  display: flex;
  justify-content: space-between;
  max-width: 900px;
  margin: 0 auto;
}
.content .prev,
.content .next {
  margin-left: -12px;
  width: 50%;
  padding: 0 12px;
}
.content .prev .pager,
.content .next .pager {
  display: inline-block;
}
.content .prev .label,
.content .next .label {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 8px;
  font-size: 14px;
  color: #bfbfbf;
}
.content .prev .title,
.content .next .title {
  font-size: 16px;
  font-weight: 500;
  color: #262626;
  transition: color 0.35s ease;
}
.content .prev .pager {
  padding: 32px 16px 32px 0;
}
.content .next {
  text-align: right;
}
.content .next .pager {
  padding: 32px 0 32px 16px;
}
.content .next .pager .label {
  justify-content: flex-end;
}
